@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* 회원 공통 */
// 인증 타입 선택 버튼
.cert_type_box {
	@include mixins.justify_width();
	align-items: stretch;

	.btn_cert_type {
		@include mixins.justify_width(center, center, column, inline);
		flex: 1;
		height: 124px;
		padding: 0 14px;
		background: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 10px;
		text-align: center;
		box-sizing: border-box;

		.text {
			display: block;
			margin-top: 12px;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
		}

		[class^=ico_] {
			display: inline-block;
			height: 35px;
			margin-top: 2px;
			vertical-align: top;
		}

		.ico_phone {
			width: 35px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_phone', 'png', 35px, 35px);
		}

		.ico_ipin {
			width: 37px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_ipin', 'png', 37px, 31px);
		}

		& + .btn_cert_type {
			margin-left: 5px;
		}
	}

	& + .info_text_box {
		margin-top: 30px;
	}

	& + .info_text {
		margin-top: 10px;
	}
}

// sns 계정 인증
.sns_cert_list {
	width: 920px;
	font-size: 0;

	.sns_cert_item {
		width: 300px;
		display: inline-block;
		vertical-align: top;

		.btn_lg {
			width: 100%;

			[class^=ico_cert_] {
				display: inline-block;
				margin-right: 12px;
				vertical-align: top;

				&.ico_cert_naver {
					width: 16px;
					height: 15px;
					@include mixins.ratio_images('../images/ico_cert_naver', 'png', 16px, 15px);
				}

				&.ico_cert_kakao {
					width: 19px;
					height: 18px;
					@include mixins.ratio_images('../images/ico_cert_kakao', 'png', 19px, 18px);
				}

				&.ico_cert_google {
					width: 16px;
					height: 17px;
					@include mixins.ratio_images('../images/ico_cert_google', 'png', 16px, 17px);
				}

				&.ico_cert_fb {
					width: 11px;
					height: 21px;
					@include mixins.ratio_images('../images/ico_cert_fb', 'png', 11px, 21px);
				}

				&.ico_cert_apple {
					width: 16px;
					height: 18px;
					@include mixins.ratio_images('../images/ico_cert_apple', 'png', 16px, 18px);
				}
			}

		}

		~ .sns_cert_item {
			margin-left: 10px;
		}

		&:nth-child(3n+1) {
			margin-left: 0;
		}

		&:nth-child(n+4) {
			margin-top: 12px;
		}
	}
}

// 심사 결과
.judgment_state_box {
	margin-bottom: 50px;
	font-size: 0;

	.judgement_date {
		display: inline-block;
		margin-left: 6px;
		@include mixins.font_size_xs;
		color: variables.$textColorLightGray;
		vertical-align: top;
	}

	.round_gray_box {
		margin-top: 20px;
		padding-left: 41px;

		.reply_desc {
			position: relative;
			@include mixins.font_size_xs;

			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: -17px;
				width: 12px;
				height: 12px;
				@include mixins.ratio_images('../images/ico_judgment_reply', 'png', 12px, 12px);
			}

			+ .bul_item_asterisk {
				margin-top: 10px;
			}
		}

	}
}

// 아이디
.find_id_box {
	@include mixins.justify_width(center, center);
	height: 90px;
	margin-bottom: 10px;
	border-radius: 6px;
	background-color: variables.$colorGray04;

	.id_val {
		@include mixins.font_size_xs;
		font-weight: variables.$fontWeightBold;
	}

	~ .form_wrap {
		margin-top: 30px;
	}
}

//
.usage_status_group {
	@include mixins.justify_width;
	align-items: stretch;
	margin-bottom: 24px;

	.usage_status_box {
		@include mixins.justify_width(center);
		flex: 1;
		padding: 35px 29px;
		border-radius: 20px;
		border: 1px solid variables.$colorGray04;

		.usage_status_list {
			width: 100%;

			.usage_status_item {
				@include mixins.justify_width;

				.usage_title {
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;

					+ .tooltip_wrap {
						margin: 3px 0 0 6px;
					}
				}

				.right_box {
					@include mixins.justify_width(center);
					margin-left: auto;

					.btn_text_link {
						text-decoration: underline;

						+ .btn_xxs {
							margin-left: 6px;
						}
					}

					.status_text {
						display: inline-block;
						@include mixins.font_size_xs;
						color: variables.$textColorPurple;
						vertical-align: top;

						+ .btn_xxs {
							margin-left: 6px;
						}
					}
				}

				~ .usage_status_item {
					margin-top: 24px;
				}
			}
		}

		~ .usage_status_box {
			margin-left: 24px;
		}
	}

	.tooltip_wrap {
		.usage_detail_list {
			.usage_detail_item {
				@include mixins.justify_width(center);
				color: variables.$textColorDarkGray;

				.status_title, .status_val {
					@include mixins.font_size_xxs;
				}

				.status_val {
					margin-left: auto;
				}

				~ .usage_detail_item {
					margin-top: 10px;
				}
			}
		}
	}
}

.secession_fail_desc {
	height: 72px;
	padding: 24px 10px;
	margin-bottom: 30px;
	border-radius: 10px;
	background-color: rgba(236, 31, 45, .04);
	text-align: center;
	box-sizing: border-box;

	.secession_fail_inner {
		.desc {
			@include mixins.font_size_def;
			color: variables.$textColorRed;
		}

		.tooltip_wrap {
			margin-top: 3px;
		}

		&:before {
			content: '';
			display: inline-block;
			width: 10px;
			height: 8px;
			@include mixins.ratio_images('../images/ico_member_secession_guide_text', 'png', 10px, 8px);
			vertical-align: top;
		}

		&:after {
			content: '';
			display: inline-block;
			width: 10px;
			height: 8px;
			@include mixins.ratio_images('../images/ico_member_secession_guide_text_reverse', 'png', 10px, 8px);
			vertical-align: top;
		}
	}
}

// 등급 상태 게이지
.state_gauge_wrap {
	display: block;
	width: 100%;

	.state_gauge_box {
		display: inline-block;
		width: 100%;
		vertical-align: top;

		.state_gauge_title {
			margin-bottom: 13px;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			vertical-align: top;

			.val {
				font-weight: variables.$fontWeightBold;
			}

			.tooltip_wrap {
				margin: 1px 0 0 6px;
			}

			.right_area {
				float: right;

				.accumulate_point {
					display: inline-block;
					color: variables.$textColorBlack;
					@include mixins.font_size_xs;
					vertical-align: top;

					.val {
						color: variables.$textColorPurple;
					}
				}
			}
		}

		.state_gauge_bar {
			display: block;

			.state_bar_box {
				display: block;
				position: relative;

				.state_bar {
					position: relative;
					display: block;
					width: 100%;
					height: 5px;
					border-radius: 5px;
					background-color: variables.$colorGray05;

					.state_bar_inner {
						display: inline-block;
						position: absolute;
						top: 0;
						left: 0;
						height: 5px;
						border-radius: 5px;
						background: linear-gradient(to left, #b8badf, variables.$kyoboPurple);
					}

					.state_dot {
						@include mixins.justify_width(start, between);
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;

						.dot {
							display: inline-block;
							width: 5px;
							height: 5px;
							border-radius: 5px;
							background-color: rgba(0, 0, 0, 0.2);
							vertical-align: top;
						}
					}
				}
			}

			.state_label_box {
				display: block;
				position: relative;
				margin-top: 7px;
				padding-bottom: 18px;

				.state_label {
					display: inline-block;
					position: absolute;
					left: 0;
					top: 0;
					color: variables.$textColorGray;
					@include mixins.font_size_min;
					vertical-align: top;
					@include mixins.transform(translateX(-50%));

					&.active {
						color: variables.$textColorPurple;
						font-weight: variables.$fontWeightBold;
					}

					&:first-child {
						left: 0;
						@include mixins.transform(translateX(0));
					}

					&:nth-child(2) {
						left: calc(100% / 2);
					}

					&:last-child {
						left: 100%;
						@include mixins.transform(translateX(-100%));
					}
				}
			}
		}
	}
}

.wrapper {
	&.member_login {
		@include mixins.justify_width(start, start, column);
		min-height: 100%;
		padding-top: 100px;
		box-sizing: border-box;

		.header_wrapper {
			width: 100%;
			border-bottom: none;
			flex-shrink: 0;
			height: 54px;

			.header_inner {
				width: 400px;
				margin: 0 auto;
				text-align: center;
				font-size: 0;
			}

			.logo_box {
				display: inline-block;
				width: 179px;
				height: 54px;
				vertical-align: top;

				.logo_link {
					display: block;
					height: 100%;
				}
			}

			.inflow_channel_box {
				display: inline-block;
				font-size: 0;
				vertical-align: bottom;
				@include mixins.transform(translateY(-1px));

				&:before {
					content: '';
					display: inline-block;
					width: 12px;
					height: 12px;
					margin: 0 16px 4px 12px;
					@include mixins.ratio_images('../images/ico_channel_inflow', 'png', 12px, 12px);
					vertical-align: bottom;
				}

				.inflow_channel_link {
					display: inline-block;
					height: 100%;
					vertical-align: bottom;
				}
			}
		}

		.container_wrapper {
			flex: 1 0 auto;
			width: 100%;
			padding: 50px 0 100px;
			min-height: calc(100vh - 54px - 68px - 250px);

			.contents_wrap {
				width: 400px;
				margin: 0 auto;
				padding-top: 0;

				&.login {
					width: 340px;
				}

				// login
				.login_form_wrap {
					padding-bottom: 40px;
					border-bottom: 1px solid variables.$colorGray03;

					.title_wrap {
						text-align: center;

						.title_heading {
							width: 100%;

							.next_ico {
								&:after {
									content: '';
									display: inline-block;
									margin-left: 6px;
									background-repeat: no-repeat;
									background-position: center;
								}

								&.kakao {
									&.kakao {
										&:after {
											width: 24px;
											@include mixins.ratio_images('../images/ico_sns_kakao_sm', 'png', 24px, 24px);
										}
									}
								}
							}
						}

						&.title_size_md {
							.title_heading {
								.next_ico {
									&:after {
										height: 24px;
										vertical-align: -5px;
									}
								}
							}
						}
					}

					.form_col_group {
						&.valid_check {
							&.fail {
								.col_box {
									input {
										border-color: variables.$textColorRed;
									}
								}
							}

							.col_box {
								&.fail {
									input {
										border-color: variables.$textColorRed;
									}

									~ .valid_desc {
										display: block;
									}
								}

								&.id {
									.form_desc {
										display: none;
									}

									&.focus {
										.form_desc {
											display: block;
										}
									}
								}
							}

							.valid_desc {
								padding-left: 16px;
								color: variables.$textColorRed;

								&:before {
									content: '';
									display: inline-block;
									position: absolute;
									left: 0;
									top: 3px;
									width: 12px;
									height: 12px;
									background: {
										position: left top;
									}
									@include mixins.ratio_images('../images/ico_warning', 'png', 12px, 12px);
								}
							}
						}
					}

					.btn_wrap {
						margin-top: 20px;
					}

					.save_id_box {
						margin-top: 12px;
						@include mixins.justify_width(center);

						.btn_text_link {
							margin-left: auto;
						}
					}

					.form_box, .form_col_group {
						& + .chk_row_list {
							margin-top: 20px;
							margin-bottom: 8px;
						}
					}

					.sns_login_box {
						margin: 40px 0 -16px;

						.sns_login_list {
							.sns_login_item {
								~ .sns_login_item {
									margin-left: 30px;
								}
							}
						}
					}
				}

				.join_induce_wrap {
					margin-top: 40px;
					text-align: center;

					.title_wrap {
						text-align: center;

						.title_heading {
							width: 100%;
						}

						.info_text {
							margin-top: 6px;
						}
					}

					.info_text {
						margin-top: 16px;
						color: variables.$textColorLightGray;
					}

					.btn_wrap {
						margin-top: 24px;
					}

					.col_box {
						.btn_wrap {
							margin-top: 0;
						}
					}
				}

				.login_banner_box {
					margin-top: 40px;

					.banner_link {
						img {
							display: block;
							width: 100%;
						}
					}
				}

				.captcha_wrap {
					margin-top: 20px;

					.captcha_inner {
						position: relative;
						padding-right: 40px;

						.captcha_img_box {
							width: 300px;
							height: 72px;
							border: 1px solid variables.$colorGray03;
							overflow: hidden;
							border-radius: 6px;
							background-color: #f9f9fb;
							box-sizing: border-box;

							img {
								width: 100%;
							}
						}

						.btn_wrap {
							position: absolute;
							right: 0;
							top: 0;
							width: 34px;
							height: 100%;
							margin: 0;

							.btn_captcha {
								width: 34px;
								height: 34px;
								border: 1px solid variables.$colorGray03;
								border-radius: 6px;

								.ico_refresh {
									display: inline-block;
									width: 13px;
									height: 13px;
									@include mixins.ratio_images('../images/ico_captcha_refresh', 'png', 13px, 13px);
									vertical-align: top;
								}

								.ico_speak {
									display: inline-block;
									width: 14px;
									height: 13px;
									@include mixins.ratio_images('../images/ico_captcha_speak', 'png', 14px, 13px);
									vertical-align: top;
								}

								+ .btn_captcha {
									margin: 4px 0 0;
								}
							}
						}
					}

					.form_ip {
						margin-top: 10px;
					}
				}

				// 회원 상단 아이콘 + 텍스트 안내 영역
				.member_guide_wrap {
					display: block;
					margin-bottom: 30px;

					.member_guide_title {
						@include mixins.justify_width(center);

						.ico_img_box {
							flex: 0 auto;
							margin-right: 12px;
						}

						.guide_title {
							display: inline-block;
							flex: 1;
							color: variables.$textColorBlack;
							@include mixins.font_size_def;
							font-weight: variables.$fontWeightMedium;

							.warning {
								color: variables.$textColorRed;
							}
						}
					}

					.member_guide_cont {
						margin-top: 20px;

						.info_text {
							& + .form_chk,
							& + .form_rdo {
								margin-top: 20px;
							}

							& + .bul_item_asterisk {
								margin-top: 10px;
							}
						}
					}
				}

				// 아이디 찾기 결과 리스트
				.id_result_list_wrap {
					display: block;

					.custom_scroll_wrap {
						// header : 153px, contents 상하여백 : 150px, 상단 타이틀 : 111px, 하단 안내문구 : 82px, 하단 버튼 : 90px, footer : 68px;
						max-height: calc(100vh - 153px - 150px - 111px - 82px - 90px - 68px);
					}

					// id item
					.id_result_list_item {
						@include mixins.justify_width(center);
						flex-wrap: wrap;
						box-sizing: border-box;

						.left_area {
							flex: 1;

							.id_name_box {
								word-break: break-all;
								font-size: 0;

								.form_rdo {
									margin-bottom: 4px;
								}

								.id_label {
									display: inline-block;
									margin-right: 8px;
									color: variables.$textColorGray;
									@include mixins.font_size_sm;
									font-weight: variables.$fontWeightRegular;
								}

								.id_name {
									display: inline-block;
									color: variables.$textColorBlack;
									@include mixins.font_size_sm;
									font-weight: variables.$fontWeightBold;
								}
							}

							.id_info_box {
								display: block;
								margin-top: 4px;
								padding-left: 27px;

								.id_info_inner {
									display: block;
									font-size: 0;

									.id_info {
										display: inline-block;
										color: variables.$textColorLightGray;
										@include mixins.font_size_xxs;
										font-weight: variables.$fontWeightRegular;

										.date {
											font-weight: variables.$fontWeightMedium;
										}
									}

									.gap {
										display: inline-block;
										width: 1px;
										height: 9px;
										margin: 4px 8px 0;
										background-color: #ccc;
										font-size: 0;
										vertical-align: top;
									}

									.badge_sm {
										margin-left: 8px;
									}
								}
							}
						}

						.right_area {
							flex: 0 auto;
							margin-left: 20px;
						}

						.info_text_box {
							width: 100%;
							margin-top: 13px;
						}

						& ~ .id_result_list_item {
							padding-top: 24px;
							margin-top: 24px;
							border-top: 1px solid variables.$colorGray04;
						}

						&:only-child {
							padding-bottom: 30px;
							border-bottom: 1px solid variables.$colorGray04;
						}

						&.no_rdo {
							.left_area {
								.id_info_box {
									padding-left: 0;
								}
							}
						}
					}

					.id_result_list {
						& + .id_result_list {
							margin-top: 40px;
						}
					}

					& + .info_text_box {
						margin-top: 24px;
					}
				}

				.info_text_box {
					.btn_link_member {
						color: variables.$textColorBlack;
					}
				}

				.btn_wrap {
					&.page_bottom {
						margin-top: 40px;
					}
				}

				.fold_box_wrap {
					.fold_box_list {
						.fold_box {
							.fold_box_contents {
								.cert_type_box {
									.btn_cert_type {
										height: 115px;
									}
								}
							}

							.cert_guide_box {
								margin-top: 10px;

								.info_text {
									& + .bul_item_asterisk {
										margin-top: 6px;
									}
								}
							}
						}

					}
				}

				// 회원가입 완료
				.join_complete_wrap {
					position: relative;
					padding-top: 30px;

					.title_wrap {
						justify-content: center;
						text-align: center;
					}

					.join_coupon_box {
						position: relative;
						text-align: center;

						&:after {
							content: '쿠폰 디자인 정의 후 작업';
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 135px;
							background-color: rgba(255, 0, 0, .5);
							color: #fff;
						}

						& + .round_gray_box {
							margin-top: 60px;

							& + .btn_wrap {
								margin-top: 20px;

								& + .btn_text_link {
									margin-top: 24px;
								}
							}

						}
					}

					.btn_wrap {
						+ .btn_wrap {
							margin-top: 24px;
						}
					}

					&:before {
						content: '';
						position: absolute;
						top: -30px;
						left: 50%;
