// 클럽카드
.club_card_item {
	box-sizing: border-box;
	min-height: 241px;
	border-radius: 16px;
	border: 1px solid variables.$colorGray03;

	.card_info {
		position: relative;
		padding: 24px 24px 29px;

		.card_date {
			position: absolute;
			right: 24px;
			top: 24px;
			@include mixins.font_size_xxs();
			color: variables.$textColorLightGray;

			em {
				color: #000;
				font-weight: variables.$fontWeightMedium;
			}
		}

		.card_name {
			margin: 40px 0 0;
			@include mixins.font_size_sm();
			color: #000;
			font-weight: variables.$fontWeightMedium;
		}

		.card_num {
			margin: 7px 0 0;
			@include mixins.font_size_lg();
			color: #000;
			font-weight: variables.$fontWeightBold;
		}
	}

	.card_util {
		overflow: hidden;
		@include mixins.justify_width();
		border-radius: 0 0 16px 16px;

		.card_util_col {
			position: relative;
			width: 50%;

			button {
				display: block;
				width: 100%;
				padding: 20px 0;
				@include mixins.font_size_xxs();

				color: variables.$textColorLightGray;
				background-color: variables.$colorGray06;
				cursor: pointer;
			}

			&:before {
				display: block;
				content: "";
				position: absolute;
				left: 0;
				top: 50%;
				width: 1px;
				height: 10px;
				margin: -5px 0 0;

				background-color: variables.$colorGray03;
			}

			&:first-child:before {
				display: none;
			}
		}
	}

	&.cancel {
		background-color: variables.$colorGray06;

		.card_info {
			.card_name {
				color: variables.$textColorLightGray;
			}

			.card_num {
				color: variables.$textColorLightGray;
			}
		}

		.card_util {
			display: none;
		}

	}
}

